.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 225px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 60%;
  overflow: hidden;
  text-align: center;
  background:white;
}
.card > .img-container > img {
  width: 55%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.cointext{
  font-size: 18px;
  margin-left: 5px;
}

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.header {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  
  color: rgb(238, 6, 172);
  margin-bottom: 5px;
}

.message{
  font-size:2rem;
  color: rgb(243, 188, 7);
}

.score{
  font-size:2rem;
  color: rgb(7, 243, 7);
}
.jumbo {
  width: 100%;
  text-align: center;
  font-size: 2rem;
  /* background: rgb(238, 6, 172);

  color: rgb(61, 21, 204); */
  color: rgb(238, 6, 172);

  background: rgb(61, 21, 204);
  border: 2px dotted white;
 
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

